export default{
  data () {
    return {
      tabs: ['收支明细', '兑换记录'],
      tabIndex: 0,
      detail: {
        integral_total: 0,
        today_get_integral_limit_total: 100,
        today_get_integral_total: 100
      },
      left: '',
      menuList: {
        total: 1,
        per_page: 15,
        current_page: 1,
        last_page: 1,
        data: []
      }
    }
  },
  onLoad () {
    this.getDetail()
    // 积分明细
    this.getParticulars()
  },
  methods: {
    getDetail() {
      let that= this,
        params= {
          url: that.$api.userIntegralDetails
        }
      that.$httpRequest(params).then(res => {
        that.detail= res.data
        that.left=  res.data.today_get_integral_total / res.data.today_get_integral_limit_total * 100 + '%'
      })
    },
    
    // 获取积分明细列表
    getParticulars() {
      let that= this,
        params= {
          url: that.$api.integralLogs,
          data: {
            type: 'all'
          }
        }
      that.$httpRequest(params).then(res => {
        console.log('积分明细', res)
        that.menuList= res.data.list
      })
    },
    
    // 获取积分明细列表
    getConvertList() {
      let that= this,
        params= {
          url: that.$api.lists,
          data: {
            dataType: 'all'
          }
        }
      that.$httpRequest(params).then(res => {
        console.log('兑换明细', res)
      })
    },
    
    goRules() {
      uni.navigateTo({
        url: './rules'
      })
    },
    goDetail() {
      uni.navigateTo({
        url: '../mine/todayExperience'
      })
    },
    selectTab(index) {
      this.tabIndex = index
    }
  }
}
